import React from "react";
import { Grid, Typography } from "@material-ui/core";
import { InputField, SelectField } from "../../FormFields";

const region = [
  {
    value: "1",
    label: "Metro Manila = AED 279",
  },
  {
    value: "2",
    label: "Luzon = AED 299",
  },
  {
    value: "3",
    label: "Visayas = AED 359",
  },
  {
    value: "4",
    label: "Mindanao = AED 369",
  },
];

export default function ProductOptionForm(props) {
  const {
    formField: { quantity, destination },
  } = props;
  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Options
      </Typography>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={12}>
          <InputField
            name={quantity.name}
            label={quantity.label}
            type="number"
            fullWidth
          />
        </Grid>
        <Grid item xs={12} sm={12}>
          <SelectField
            name={destination.name}
            label={destination.label}
            data={region}
            fullWidth
          />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}
