import React from "react";
import { InputField } from "../../FormFields";

export default function LoginForm(props) {
  const {
    formField: { email, password },
  } = props;
  return (
    <React.Fragment>
      <InputField
        name={email.name}
        label={email.label}
        variant="outlined"
        margin="normal"
        required
        fullWidth
        id="email"
        autoComplete="email"
        autoFocus
      />

      <InputField
        name={password.name}
        label={password.label}
        fullWidth
        variant="outlined"
        margin="normal"
        required
        type="password"
        id="password"
        autoComplete="current-password"
      />
    </React.Fragment>
  );
}
