import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { Formik, Form } from "formik";
import axios from "axios";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
// import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import CircularProgress from "@material-ui/core/CircularProgress";

import { signin } from "../actions/userActions";
import LoginValSchema from "../components/Forms/Login/LoginValSchema";
import LoginFormModel from "../components/Forms/Login/LoginFormModel";
import LoginFormInitVal from "../components/Forms/Login/LoginFormInitVal";
import LoginForm from "../components/Forms/Login/LoginForm";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright © "}
      <Link color="inherit" href="#">
        LingPhil
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  buttonProgress: {
    textAlign: "center",
  },
}));

const { formId, loginFormField } = LoginFormModel;

export default function LoginScreen(props) {
  const classes = useStyles();
  const currentValidationSchema = LoginValSchema[0];

  const userSignin = useSelector((state) => state.userSignin);
  const { loading, userInfo, error } = userSignin;
  const dispatch = useDispatch();

  // const [state, setState] = useState({
  //   isLoading: false,
  // });

  const redirect = props.location.search
    ? props.location.search.split("=")[1]
    : "/";

  useEffect(() => {
    if (userInfo) {
      props.history.push(redirect);
    }
    return () => {
      //
    };
  }, [userInfo]);

  //   const submitHandler = (e) => {
  //     e.preventDefault();
  //     dispatch(signin(email, password));
  //   };

  function _sleep(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  // function stopLoading() {
  //   setState({ isLoading: false });
  // }

  function _handleSubmit(values, actions) {
    // setState({ isLoading: true });
    _submitForm(values, actions);
  }

  async function _submitForm(values, actions) {
    // await _sleep(2000);
    dispatch(signin(values.email, values.password));
    // stopLoading();
    actions.setSubmitting(false);
    // alert(JSON.stringify(values, null, 2));
  }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign in
        </Typography>
        <Typography component="h1" variant="h5">
          {loading && (
            <div>
              <CircularProgress size={24} className={classes.buttonProgress} />
            </div>
          )}
          {error && <div>{error}</div>}
        </Typography>

        <Formik
          // enableReinitialize
          initialValues={LoginFormInitVal}
          validationSchema={currentValidationSchema}
          onSubmit={_handleSubmit}
        >
          {({ isSubmitting }) => (
            <Form id={formId} className={classes.form}>
              <LoginForm formField={loginFormField} />
              <FormControlLabel
                control={<Checkbox value="remember" color="primary" />}
                label="Remember me"
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                disabled={isSubmitting}
              >
                Sign In
              </Button>

              <Grid container>
                <Grid item sm={12} className={classes.buttonProgress}>
                  {isSubmitting && <CircularProgress size={24} />}
                </Grid>
                <Grid item xs>
                  <Link href="#" variant="body2">
                    Forgot password?
                  </Link>
                </Grid>
                <Grid item>
                  <Link href="#" variant="body2">
                    {"Don't have an account? Sign Up"}
                  </Link>
                </Grid>
              </Grid>
            </Form>
          )}
        </Formik>
      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
  );
}
