const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(1, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(10),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardMedia: {
    paddingTop: "56.25%", // 16:9
    height: 250,
    width: 200,
  },
  cardContent: {
    flexGrow: 1,
  },

  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  mainFeaturedPost: {
    position: "relative",
    // backgroundColor: theme.palette.grey[800],
    // color: theme.palette.common.white,
    // marginBottom: theme.spacing(1),
    // backgroundImage: "url(https://source.unsplash.com/random)",
    backgroundImage: `url(${lingphil})`,
    backgroundSize: "contain",
    backgroundRepeat: "no-repeat",
    display: "block",
    maxHeight: "100%",
    // maxWidth: "100%",
  },
  overlay: {
    position: "absolute",
    top: 0,
    bottom: 0,
    right: 0,
    left: 0,
    backgroundColor: "rgba(0,0,0,.3)",
  },
  mainFeaturedPostContent: {
    position: "relative",
    padding: theme.spacing(6),
    [theme.breakpoints.up("md")]: {
      padding: theme.spacing(6),
      paddingRight: 0,
    },
    height: "170px",
  },
  cardHeader: {
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.success.main
        : theme.palette.grey[700],
    // theme.palette.type === "light"
    //   ? theme.palette.grey[300]
    //   : theme.palette.grey[700],
  },
  cardPricing: {
    display: "flex",
    justifyContent: "center",
    alignItems: "baseline",
    marginBottom: theme.spacing(2),
  },
  cardBorder: {
    borderStyle: "solid",
    borderWidth: "2px",
    borderColor: "#ccc",
    borderRadius: "5px",
  },
}));

const tiers = [
  {
    _id: "1",
    promoPosition: "start",
    title: "Family Package",
    price: "459",
    description: [
      "3 Jumbo boxes",
      "plus 2 Bulilit boxes",
      "To Manila = 459",
      "To Luzon = 499",
      "To Vis/Min = 599",
    ],
    buttonText: "See more",
    buttonVariant: "outlined",
  },
  {
    _id: "2",
    promoPosition: "middle",
    title: "Super Mega + 2 Bulilits",
    subheader: "Most popular",
    price: "279",
    description: [
      "1 Super Mega Box",
      "plus 2 Bulilit boxes",
      "To Manila = 279",
      "To Luzon = 299",
      "To Visayas = 359",
      "To Mindanao = 369",
    ],
    buttonText: "See more",
    buttonVariant: "contained",
  },
  {
    _id: "3",
    promoPosition: "end",
    title: "Barkada Package",
    price: "759",
    description: [
      "5 Jumbo boxes",
      "plus 3 Bulilit boxes",
      "To Manila = 759",
      "To Luzon = 829",
      "To Vis/Min = 999",
    ],
    buttonText: "See more",
    buttonVariant: "outlined",
  },
];

const cards = [1, 2, 3];
const cardImages = [
  {
    image: megaBox,
    title: "Super Mega Box",
    description: "50 x  73 x 97 cm",
  },
  {
    image: smallBox,
    title: "Bulilit Box",
    description: "40 x  40 x 40 cm",
  },
  {
    image: jumboBox,
    title: "Jumbo Box",
    description: "51 x  51 x 97 cm",
  },
];
const post = {
  // image: "https://source.unsplash.com/random",
  image: lingphil,
  imageText: "Sample",
  title: "Title",
  description: "Description",
  linkText: "Link",
};

export default function Promos() {
  const classes = useStyles();

  return (
    <React.Fragment>
      {/* <CssBaseline /> */}
      <main>
        {/* Hero unit */}
        <div className={classes.heroContent}>
          <Container maxWidth="md">
            {/* Rael - Paper wrapper adds border and shadow which is visible when the image is smaller than the container */}
            {/* <Paper
              className={classes.mainFeaturedPost}
              style={{
                backgroundImage: `url(${post.image})`,
              }}
            > */}
            <div className={classes.mainFeaturedPost}>
              {/* Increase the priority of the hero background image */}
              {/* {
                <img
                  style={{ display: "block" }}
                  src={post.image}
                  alt={post.imageText}
                />
              } */}
              {/* <div className={classes.overlay} /> */}
              <Grid container>
                <Grid item md={6}>
                  <div className={classes.mainFeaturedPostContent}>
                    {/* <Typography
                      component="h1"
                      variant="h3"
                      color="inherit"
                      gutterBottom
                    >
                      {post.title}
                    </Typography>
                    <Typography variant="h5" color="inherit" paragraph>
                      {post.description}
                    </Typography>
                    <Link variant="subtitle1" href="#">
                      {post.linkText}
                    </Link> */}
                  </div>
                </Grid>
              </Grid>
            </div>
            {/* </Paper> */}
          </Container>
        </div>
        {/* End hero unit */}
        <Container maxWidth="md" component="main">
          <Grid container spacing={5} alignItems="flex-end">
            {tiers.map((tier) => (
              // Enterprise card is full width at sm breakpoint
              <Grid
                item
                key={tier.title}
                xs={12}
                sm={tier.promoPosition === "end" ? 12 : 6}
                md={4}
              >
                {/* border: 1, borderColor: "black" */}
                <Card classes={{ root: classes.cardBorder }}>
                  <CardHeader
                    title={tier.title}
                    subheader={tier.subheader}
                    titleTypographyProps={{ align: "center" }}
                    subheaderTypographyProps={{ align: "center" }}
                    action={
                      tier.promoPosition === "middle" ? <StarIcon /> : null
                    }
                    className={classes.cardHeader}
                  />
                  <CardContent>
                    <div className={classes.cardPricing}>
                      <div style={{ display: "flex" }}>
                        <Typography
                          component="h3"
                          variant="h3"
                          color="textSecondary"
                          noWrap
                        >
                          AED
                        </Typography>
                        <Typography
                          component="h2"
                          variant="h3"
                          color="textPrimary"
                          style={{ marginLeft: 10 }}
                          noWrap
                        >
                          {tier.price}
                        </Typography>
                        {/* <Typography variant="h6" color="textSecondary">
                        /mo
                      </Typography> */}
                      </div>
                    </div>
                    <ul>
                      {tier.description.map((line) => (
                        <Typography
                          component="li"
                          variant="subtitle1"
                          align="center"
                          key={line}
                        >
                          {line}
                        </Typography>
                      ))}
                    </ul>
                  </CardContent>
                  <CardActions>
                    <Link to={"/product/" + tier._id}>
                      <Button
                        fullWidth
                        variant={tier.buttonVariant}
                        color="primary"
                      >
                        {tier.buttonText}
                      </Button>
                    </Link>
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
        <Container className={classes.cardGrid} maxWidth="md">
          <Grid container spacing={4}>
            {cardImages.map((card) => (
              <Grid item key={card} xs={12} sm={6} md={4}>
                <Card className={classes.card}>
                  <CardMedia
                    className={classes.cardMedia}
                    // image="https://source.unsplash.com/random"
                    image={card.image}
                    title="Image title"
                  />
                  <CardContent className={classes.cardContent}>
                    <Typography gutterBottom variant="h5" component="h2">
                      {card.title}
                    </Typography>
                    <Typography>{card.description}</Typography>
                  </CardContent>
                  <CardActions>
                    <Button size="small" color="primary">
                      View
                    </Button>
                    {/* <Button size="small" color="primary">
                      Edit
                    </Button> */}
                  </CardActions>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </main>
      {/* Footer */}
      {/* <footer className={classes.footer}>
        <Typography variant="h6" align="center" gutterBottom>
          Footer
        </Typography>
        <Typography
          variant="subtitle1"
          align="center"
          color="textSecondary"
          component="p"
        >
          Something here to give the footer a purpose!
        </Typography>
        <Copyright />
      </footer> */}
      {/* End footer */}
    </React.Fragment>
  );
}
