import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { detailsProduct } from "../actions/productActions";
import Button from "@material-ui/core/Button";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import CircularProgress from "@material-ui/core/CircularProgress";
import { Formik, Form } from "formik";
import ProductOptionValSchema from "../components/Forms/ProductOptions/ProductOptionValSchema";
import ProductOptionFormModel from "../components/Forms/ProductOptions/ProductOptionFormModel";
import ProductOptionFormInitVal from "../components/Forms/ProductOptions/ProductOptionFormInitVal";
import ProductOptionForm from "../components/Forms/ProductOptions/ProductOptionForm";

import jumboBox from "./../images/jumbo-box1.png";

const useStyles = makeStyles((theme) => ({
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardMedia: {
    height: 300,
    width: 300,
  },
  cardBorder: {
    borderStyle: "solid",
    borderWidth: "2px",
    borderColor: "#ccc",
    borderRadius: "5px",
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  centerAlign: {
    textAlign: "center",
  },
}));

const cardImage = {
  pic: jumboBox,
  title: "Super Mega Box",
  description: "50 x  73 x 97 cm",
};

const { formId, productOptionFormField } = ProductOptionFormModel;

function _sleep(ms) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

function _handleSubmit(values, actions) {
  // setState({ isLoading: true });
  _submitForm(values, actions);
}

async function _submitForm(values, actions) {
  // await _sleep(2000);
  //dispatch(signin(values.email, values.password));
  actions.setSubmitting(false);
  // alert(JSON.stringify(values, null, 2));
}

export default function ProductPage() {
  const classes = useStyles();
  const currentValidationSchema = ProductOptionValSchema[0];

  const userSignin = useSelector((state) => state.userSignin);
  const { loading, userInfo, error } = userSignin;
  const dispatch = useDispatch();

  return (
    <React.Fragment>
      <Container maxWidth="md" component="main">
        <Grid container spacing={5} alignItems="flex-end">
          <Grid item xs={12} sm={6}>
            <Card classes={{ root: classes.cardBorder }}>
              <CardMedia
                className={classes.cardMedia}
                image={cardImage.pic}
                //image="https://source.unsplash.com/random"
                title="Image title"
              />
            </Card>
          </Grid>
          <Grid item xs={12} sm={6}>
            <Card>
              <Typography
                component="h1"
                variant="h3"
                color="inherit"
                // gutterBottom
              >
                {cardImage.title}
              </Typography>
              <Typography variant="h5" color="inherit" paragraph>
                {cardImage.description}
              </Typography>
            </Card>
          </Grid>
          <Grid item xs={12}>
            <Card>
              <Formik
                // enableReinitialize
                initialValues={ProductOptionFormInitVal}
                validationSchema={currentValidationSchema}
                onSubmit={_handleSubmit}
              >
                {({ isSubmitting }) => (
                  <Form id={formId} className={classes.form}>
                    <ProductOptionForm formField={productOptionFormField} />

                    <Link to={"/checkout"}>
                      <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        disabled={isSubmitting}
                      >
                        Go to Checkout
                      </Button>
                    </Link>
                    <Grid container>
                      <Grid item sm={12} className={classes.centerAlign}>
                        {isSubmitting && <CircularProgress size={24} />}
                      </Grid>
                    </Grid>
                  </Form>
                )}
              </Formik>
            </Card>
          </Grid>
        </Grid>
      </Container>
    </React.Fragment>
  );
}
